.about-wrap {
    .about-text cite {
        background: url(https://assets.devowl.io/in-app/devowl.io-logo.svg) no-repeat top left;
        padding: 5px 0 5px 55px;
        display: block;
        margin-top: 12px;
    }

    .wp-badge {
        color: black;
        background-repeat: no-repeat;
        background-position: center 25px;
        background-size: 90px auto;
        background-color: white;
    }

    .about-wrap-content {
        max-width: initial;
    }

    .button-hero {
        font-size: 1.2em !important;
    }

    .column.key-feature {
        > p:first-of-type {
            margin: 15px 0 0 0;
            height: 200px;
            overflow: hidden;
            text-align: center;

            img {
                width: 100%;
                height: auto;
                border-radius: 3px;
                max-width: 400px;
            }
        }
    }

    .error-msg {
        margin: 10px;
        color: red;
    }
}

.tablenav-pages {
    display: none;
}